// OVERVIEW OF MODERN JAVASCRIPT DEVELOPMENT

/*

Desenvolvimento:
Mdulos e pacotes de terceiros so criados e gerenciados com a ajuda de ferramentas como o Node Package Manager (NPM).
NPM serve como repositrio e software para gerenciar dependncias de terceiros e ferramentas de desenvolvimento.

Processo de Build:
Bundling: Os mdulos e pacotes de terceiros so combinados em um nico arquivo usando ferramentas como Webpack ou Parcel.
Transpilao/Polyfilling: Usando o Babel, o cdigo JavaScript moderno  convertido (transpilado) para ser compatvel com navegadores mais antigos (ES5).

Produo:
O resultado final  um arquivo JavaScript nico e otimizado, pronto para ser usado na produo.
Essencialmente, o slide mostra como ferramentas como NPM, Webpack/Parcel e Babel integram mdulos e pacotes em um fluxo moderno de desenvolvimento JavaScript.

WEBPACKS VS PARCEL

Parcel: Se voc quer algo que funcione "out of the box" com configurao zero e  ideal para prottipos, projetos pequenos ou mdios, ou se voc est focado em produtividade imediata, Parcel  a escolha.

Webpack: Se voc precisa de mais controle sobre como seus assets so processados, ou se est lidando com um projeto grande e complexo com mltiplas entradas e sadas, onde a otimizao de produo  crtica, Webpack  uma escolha mais adequada.
*/

