// AN OVERVIEW OF MODULES IN JAVASCRIPT
/*
1: Uma Viso Geral dos Mdulos...

Definio de Mdulo: Um mdulo  uma pea reutilizvel de cdigo que encapsula 
                     detalhes de implementao e pode ser um arquivo autnomo.

Por que Usar Mdulos?

Compor Software: Mdulos so os blocos de construo que usamos para criar aplicativos complexos.
Isolar Componentes: Permite o desenvolvimento de mdulos de forma isolada, sem pensar na base de cdigo completa.
Abstrair Cdigo: Mdulos implementam cdigo de baixo nvel que pode ser importado em outros mdulos.
Organizar Cdigo: A estrutura de mdulos resulta em uma base de cdigo mais organizada.
Reutilizar Cdigo: Facilita a reutilizao do mesmo cdigo em vrios projetos.


2: Mdulos Nativos JavaScript (ES6)...

Estrutura de Mdulo ES6: Cada mdulo  armazenado em arquivos, com exatamente um mdulo por arquivo.

Caractersticas dos Mdulos ES6:

Variveis de Topo: Escopo restrito ao mdulo.
Modo Padro: Modo estrito em comparao ao modo "solto" de scripts.
Importaes e Exportaes: Os mdulos ES6 suportam importaes e exportaes, permitindo a interao entre diferentes mdulos.
HTML Linking: Deve ser feito com <script type="module">.
Carregamento de Arquivos: O carregamento  assncrono.


3: Como os Mdulos ES6 So Importados...

Importao Sincrnica: Os mdulos so importados de maneira sncrona, graas a importaes de nvel superior
                       (estticas) que tornam as importaes conhecidas antes da execuo.

Processo de Importao:

O cdigo do arquivo  analisado, e as importaes so baixadas de forma assncrona.
As importaes so vinculadas s exportaes dos mdulos correspondentes.
Aps a vinculao, o cdigo dos mdulos  executado em uma ordem especfica, permitindo 
a eliminao de cdigo morto e a otimizao de pacotes.
*/

