// Introduction NPM :D

/*
Nessa lecture aprendemos:
  O que  npm? 
  O NPM (Node Package Manager)  o gerenciador de pacotes padro para o Node.js e serve 
  para instalar, gerenciar e compartilhar bibliotecas e ferramentas no ecossistema JavaScript. 
  Com o NPM, voc pode instalar dependncias, bibliotecas de terceiros e ferramentas de build 
  (como bundlers), e gerenciar as verses dessas dependncias no seu projeto.

  Como instalar npm: npm --install;

  utilizar comandos bsicos do NPM como [npm init, npm i (install) lodash-es (ES6)];

  Importar um mtodo ou apenas utilizar uma API como package (isso no  prtico, ento ser resolvido com Parcel);

  Aprendemos a importncia do package.json que serve para quando formos enviar nosso 
  arquivo para outro local. Com ele, no precisamos baixar cada package novamente, apenas damos 
  comando npm i para baixar qualquer package que estava no arquivo package.json

*/

// import cloneDeep from './node_modules/lodash-es/cloneDeep.js';
import cloneDeep from 'lodash-es';

const state = {
  cart: [
    { product: 'bread', quantity: 5 },
    { product: 'pizza', quantity: 5 },
  ],
  user: { loggedIn: true },
};
const stateClone = Object.assign({}, state);
const stateCloneDeep = cloneDeep(state);

state.user.loggedIn = false;
console.log(stateClone); // False, quando o original for alterado, o clone tambm ser...

console.log(stateCloneDeep); // True, aqui no temos esse problema...

