// Bundling with Parcel and NPM scripts
/* 
O que  bundle?
Um bundle  um arquivo final que rene (ou "empacota") todos os mdulos, 
arquivos e dependncias de um projeto JavaScript em um nico ou poucos arquivos
otimizados para produo. Esse processo  geralmente feito por uma ferramenta de empacotamento, 
como Webpack, Parcel ou Rollup.

Comando para instalar: npm i parcel --save-dev


Utilizar Parcel em nosso html: npx parcel index.html. Isso nos resultar um localhost:1234 
(mesma coisa do live-server, porm, com a adaptao do Parcel)

Comando mais usado para rodar o parcel 
    "scripts": {
      "start": "parcel index.html"
    },
  npm run start

Para buildar nosso projeto:
"build": "parcel build index.html"
npm run build

Para usar assets globalmente (no  o mais recomendado):
npm i parcel -g
*/

// Apenas o Parcel entende esse cdigo, o JS ignora ele
if (module.hot) {
  module.hot.accept();
}

